<template>
<div class="layout-content">
<el-descriptions
  direction="vertical"
  :column="2"
  :size="size"
  border
>
  <el-descriptions-item label="学生姓名">{{name}}</el-descriptions-item>
  <el-descriptions-item label="学生学号">{{sno}}</el-descriptions-item>
  <el-descriptions-item label="学生姓别">{{sex}}</el-descriptions-item>
  <el-descriptions-item label="学生年龄">{{age}}</el-descriptions-item>
  <el-descriptions-item label="学生班级">{{banji}}</el-descriptions-item>
  <el-descriptions-item label="已修学分">{{score}}</el-descriptions-item>
</el-descriptions>
</div>
</template>

<script>  
import { mapGetters } from 'vuex';

export default {
  computed: {
    ...mapGetters(['getUsername']),
    username() {
      return this.getUsername;  // 确保是一个函数调用
    }
  },
  data() {
    return {
      name: '',
      sno: '',
      sex: '',
      age: '',
      banji: '',
      score: ''
    };
  },
  mounted() {
    let params = new URLSearchParams();     
    params.append('username', this.username);  
    console.log(this.username)
      
    this.$axios({
      method: "post",    
      url: "http://127.0.0.1:8688/showstuinfo",    
      data: params,    
      headers: {    
        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'    
      }    
    }).then((result) => {  
        console.log(result.data);  
         this.name = result.data.name || result.data[1];   
        this.sno = result.data.sno || result.data[0];  
        this.sex = result.data.sex || result.data[2];  
        this.age = result.data.age || result.data[3];  
        this.banji = result.data.banji || result.data[4];  
        this.score = result.data.score || result.data[5];    
      }).catch((error) => {  
        console.error('请求失败:', error);  
      });
  }
};
</script>

